os programas requeridos. Para ser competitiva, una organización debe gestionar de manera estratégica la adquisición de estos recursos, dada por las dinámicas externas del entorno y el mercado. ")])]),_c('div',{staticClass:"col-md-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema2-3-acord3.png"),"alt":"Recursos tecnológicos"}})])])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"Recursos humanos"}},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("El recurso humano o capital humano es el más importante en cualquier proyecto, empresa o entidad; sin este no podría existir la organización, por ende, el proyecto. Este recurso es el que aporta el conocimiento, saberes, oficios, constancia, dedicación y aprendizaje; cada persona, de acuerdo con sus conocimientos, desempeña un rol y asume responsabilidades bien definidas. El correcto funcionamiento del área de recursos humanos, el buen ambiente laboral y las condiciones justas y equitativas propenden por tener un excelente equipo de trabajo, que al final se deriva en ganancias para todos. Un empleado contento y bien remunerado se sentirá feliz en su trabajo, contrario a un trabajador que tiene que laborar en un ambiente conflictivo, con bajo salario y extenuantes horarios laborales.")])]),_c('div',{staticClass:"col-md-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema2-3-acord4.png"),"alt":"Recursos humanos"}})])])])]),_c('Separador'),_vm._m(17),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade"}},[_vm._v("El proceso de seguimiento y control del proyecto es un conjunto de elementos y acciones que se realizan para medir y comprobar la ejecución de las tareas propuestas durante la fase inicial, establecidas en el momento de la planificación. Se le invita a profundizar más sobre este importante tema en el siguiente video:")]),_vm._m(18)],1)],1)}
